<template>
<div class="bg">
  <div v-for="(person,index) in people" :key="person.src"  >
    <img 
    :src="'/static/people/'+person.src" 
    :alt="index" 
    :title="person.src"
    @click="showCard(index)"
     class="person" :style="{ transform: transform[index]}">
  </div>
  <div>
    <transition name="fade">
      <img  @click="showDesc=true" class="card" :src="cardSrc" :alt="cardSrc" v-if="show">
    </transition>
    <transition name="down">
      <img @click="show=false;showDesc=false" src="/static/3-最深层页面.jpg" alt="" srcset="" class="card" v-show="showDesc">
    </transition>
  </div>
</div>
</template>

<script>

export default {
  name: 'App',
  cardSrc: '',
  data(){
    return {
      showDesc: false,
      show: false,
      people: [
        {
          src: '孔雀男-01.png',
          top: '30%',
          left: '',
          transform: ''
        },
        {
          src: '高富帅-02.png',
          top: '25%',
          left: '280'
        },
        {
          src: '钻石王老五-03.png',
          top: '',
          left: ''
        },
        {
          src: '食草男-04.png',
          top: '',
          left: ''
        },
        {
          src: '小鲜肉-05.png',
          top: '',
          left: ''
        },
        {
          src: '妈宝男-06.png',
          top: '',
          left: ''
        },
        {
          src: '经济适用男-07.png',
          top: '',
          left: ''
        },
        {
          src: '食肉女-01.png',
          top: '',
          left: ''
        },
        {
          src: '白富美-02.png',
          top: '',
          left: ''
        },
        {
          src: '白骨精-03.png',
          top: '',
          left: ''
        },
        {
          src: '猪猪女孩-04.png',
          top: '',
          left: ''
        },
        {
          src: '食肉男-05.png',
          top: '',
          left: ''
        },
        {
          src: '虎妈-06.png',
          top: '',
          left: ''
        },
        {
          src: '恨嫁女-06.png',
          top: '',
          left: ''
        },
      ]
    }
  },
  methods: {
    showCard(index){
      this.show = true;
      // this.people[index].transform = 'scale(2)'
      this.cardSrc = '/static/2-人物展示/'+this.people[index].src.replace('png', 'jpg');
    }
  },
  computed:{
    transform(){
      return this.people.map((person,index) => `translateX(${person.left || index*200}px) translateY(${person.top ||'200px'}) ${person.transform || ''}`)
    }
  }
}
</script>

<style>
*{
  padding: 0;
  margin: 0;
}
.card{
  position: fixed;
  width: 100%;
}
html,body{
  height: 100%;
  overflow-y: hidden;
}
.bg{
  overflow-y: hidden;
  position: relative;
  height: 100vh;
  width: calc(100vh/362*1600);
  background-image: url('./assets/bg.jpeg');
  background-size: cover;
}
.person{
  width:200px;
  transition: .3s linear all;
  position:absolute;
}
.fade-enter-active{
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.down-enter-active{
  transition: transform .5s;
}
.down-enter, .down-leave-to /* .fade-leave-active below version 2.1.8 */ {
  transform: translateY(-100vh);
}
</style>



